import React, {Component} from 'react';
import './App.css';
import {Search} from './components/Search';
import Header from './components/Header';
import {Add} from './components/Add';
import {AnnounsmentList} from './components/AnnounsmentList';
import {Edit} from './components/Edit';

export default class App extends Component {
state = {
    announcements : [{
        id:'0000',
        title: '1',
        description: 'lorem10',
        date: '22.10.1985'
    },
        {id:'0001',
            title: '11',
            description: 'lorem101111',
            date: '22.09.1985'
        }]
}
delItem = (id) => {
    this.setState(
        ({announcements}) => {
        
            return{
                announcements: announcements.filter(a => a.id!==id),
            };

        }
    );
}
    addItem = (title, description) => {
        this.setState(
            ({announcements}) => {
                let date = new Date();
                let data = date.getFullYear()+'.'+
                    date.getMonth()+'.'+date.getDate()+'  '+date.getHours()+':'+date.getMinutes()+':'+date.getSeconds();
                return{
                    announcements: [...announcements, {id: Date.now(), title: title, description: description, date: data}],
                };

            }
        );
    }
    render(){

        return (
            <>
                <div className="Header">
                    <Header />
                </div>
                <div className="Search">
                    <Search/>
                </div>
            <div className="App">
               <Add AddAnnounc={this.addItem}/>
               <AnnounsmentList
                   announcements={this.state.announcements}
                   onDeleted={this.delItem}/>

                    <Edit />

            </div>
            </>
        );
    }}
